<template>
  <div class="chart">
    <div class="chartcontainer" :is=$route.params.id>
        <fourohfour></fourohfour>
    </div>
  </div>
</template>

<script>
import ohsixonesix from './Charts/ohsixonesix'
import zidane from './Charts/zidane'
import pixar from './Charts/pixar'
import bond from './Charts/bond'
import fourohfour from './Charts/fourohfour'
export default {
  name: 'chart',
  data () {
    return {
    }
  },
  components: {
    ohsixonesix,
    zidane,
    pixar,
    bond,
    fourohfour
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    $card-width: 28vw;
    $small-screen: "only screen and (max-width : 600px)";
    .chart{
      //outline: 1px solid red;
      margin: 0;
      padding: 0;
      margin: 4vw;
      @media #{$small-screen}{
              margin: 2vw;
          }
      }

    .chart-component{
        h1{
            font-size: calc(#{$card-width} * .1);
                @media #{$small-screen}{
                    font-size: 21px;
                }
            }
        h2{
            font-size: calc(#{$card-width} * .08);
                @media #{$small-screen}{
                  font-size: 18px;
                }
            }
        p{
            font-size: calc(#{$card-width} * .05);
                @media #{$small-screen}{
                  font-size: 16px;
                }
        }
        a{
            font-size: calc(#{$card-width} * .05);
                @media #{$small-screen}{
                    font-size: 16px;
                }
        }
        label{
            padding: calc(#{$card-width} * .03);
            font-size: calc(#{$card-width} * .04);
            @media #{$small-screen}{
                padding: calc(#{$card-width} * .07);
                font-size: 13px;
            }
        }
     }
</style>



