<template>
  <div class="zidane chart-component">
    <div id="bg"></div>
    <AppHeader></AppHeader>
    <div id="header">
      <h1>Zinedine Zidane by Vaudville Smash</h1>
      <p>
        The following charts the playing position, national team, and the clubs of all the players mentioned in the song “Zinedine Zidane” by VAUDEVILLE SMASH.
      </p>
    </div>
    <div id="positions">
      <h2>Playing Position</h2>
      <svg id="positions-svg" width="700" viewBox="0 0 700 180" preserveAspectRatio style="width: 100%;" />
    </div>
    <div id="national-team">
      <h2>National Team</h2>
      <p>This chart shows the national team of each player - grouped by confederation.</p>
      <svg id="national-team-svg" width="700" viewBox="0 0 700 200" preserveAspectRatio style="width: 100%;" />
      <h2>Most Represented Nations</h2>
      <svg id="most-national-teams-svg" width="700" viewBox="0 0 700 130" preserveAspectRatio style="width: 100%;" />
    </div>
    <div id="club">
      <h2>Club</h2>
      <p>This chart shows the club each player played for during the period this song came out - grouped by confederation.</p>
      <svg id="club-svg" width="700" viewBox="0 0 700 260" preserveAspectRatio style="width: 100%;" />
    </div>
    <div id="video">
      <a href="https://youtu.be/nrOLGifnMJk">Listen to the song on <span>YouTube</span>>></a>
    </div>
  </div>
</template>

<script>
  import AppHeader from './../AppHeader'
  import {filter} from 'lodash'
  import * as d3 from 'd3'
  import data from './../../../static/charts/zidane.csv'
  export default {
    name: 'zidane',
    data () {
    return {
        data: data
      }
    },
    methods: {
      playersChart: function(selectId, chartType, propsArray, y, width, textColor, rectColor, strokeColor){

          var players = data;
          var positionsSVG = d3.select(selectId);
          positionsSVG.selectAll('text')
            .data(propsArray)
            .enter()
            .append('text')
            .text(function(d){
              var txt = chartType=='position' ? d + 's' : d
              return txt == 'None' ? 'Retired' : txt
            })
            .attr('x', 75)
            .attr('y',function(d, i){
              return y + 5 + (i * y)
            })
            .attr('font-size', 12)
            .attr('text-anchor', 'end')
            .attr('fill', textColor)

//--------------------------------------------------------------------------------
          var theData;

          positionsSVG.selectAll('g')
            .data(propsArray)
            .enter()
            .append('g')
            .attr('x', 0)
            .attr('y', y)
            .attr('transform', function(d, i){
              return 'translate(0 '+ y * i +')'
            })
            .attr('id', function(d){
              return d.replace(/\s+/g, '').toLowerCase() +'chart'
            })
            .each(function(d, i){
              var parent = d3.select(this);
              var endOfLine;
              var theData = filter(players, function(o){
                  return  o[chartType] == d});
              parent.selectAll('rect')
                .data(theData)
                .enter()
                .append('rect')
                .attr('width', width)
                .attr('height', 5)
                .attr('fill', rectColor)
                .attr('stroke', strokeColor)
                .attr('x', function(d, i){
                  endOfLine = 80 + ((width+1) * i);
                  return endOfLine;
                })
                .attr('y', y)
                .attr('rx', 1)
                .attr('ry', 1)
                .attr('data-playername', function(d){
                  return d.player
                })
                .attr('data-playerclub', function(d){
                  return d.club
                })
                .attr('data-playercountry', function(d){
                  return d.country
                })
                .attr('data-index', function(d, i){
                  return i
              })
              .on('mouseenter', function(d, i){
                var that = d3.select(this);
                var d = d;
                parent.append('g')
                  .attr('id', 'hovertext')
                  .attr('transform', 'translate(0 0)')
                  .append('text')

                  .attr('font-size', 11)
                  .attr('x', that.attr('x'))
                  .attr('y', function(){
                    if (chartType == 'position' || chartType == 'country'){
                      return that.attr('y') - 10
                    }else{
                      return that.attr('y') - 20;
                    }
                  })
                  .attr('fill', textColor)
                  .attr('stroke', textColor)
                  .attr('id', 'this-text')
                  .text(that.attr('data-playername'))
                  .attr('text-anchor', function(){
                    var bbox = parseFloat(d3.select(this).node().getBBox().width) + parseFloat(that.attr('x'));
                    var svgWidth = parseFloat(d3.select(selectId).attr('width'));
                    if (bbox > svgWidth){
                      return 'end';
                    }else{
                      return 'start';
                    }
                  })
                  .append('tspan')
                  .attr('x', that.attr('x'))
                  .attr('dy', 15)
                  .text(function(){
                    if (chartType == 'position' || chartType == 'country'){
                      d3.select(this).remove();
                      return;
                    }else if(chartType == 'confederation'){
                      return that.attr('data-playercountry')
                    }else{
                      return that.attr('data-playerclub')
                    }
                  })
                  .each(function(){
                    var bbox = parseFloat(d3.select(this).node().getBBox().width) + parseFloat(that.attr('x'));
                    var svgWidth = parseFloat(d3.select(selectId).attr('width'));
                    if(!d3.select(this).node().getBBox().width){
                      return
                    }
                    if (bbox > svgWidth){
                      d3.select('#this-text').attr('text-anchor', function(){
                        return 'end';
                      })
                    }else{
                       d3.select('#this-text').attr('text-anchor', function(){
                        return 'start';
                      })
                    }
                  })
              })
              .on('mouseleave', function(){
                d3.select('#hovertext').remove();
              })
              parent.append('text')
                .text(theData.length)
                .attr('fill', textColor)
                //.attr('stroke', 'black')
                .attr('font-size', 12)
                .attr('x', function(){
                  if (!endOfLine){
                    return 80
                  }else{
                    return endOfLine+width*1.5
                  }
                })
                .attr('y', y + 5)
            })
      }
    },
    mounted(){
        window.scrollTo(0, 0);
        document.title = 'Zinedine Zidane by Vaudville Smash - Charts';
        this.playersChart('#positions-svg', 'position', ['Goalkeeper', 'Defender', 'Midfielder', 'Winger', 'Forward'], 30, 15, '#fff', '#1e90ff', '#181818');
        this.playersChart('#national-team-svg', 'confederation', ['UEFA', 'CONMEBOL', 'CAF', 'AFC', 'CONCACAF'], 36, 10, '#fff', '#1e90ff', '#181818');
        this.playersChart('#most-national-teams-svg', 'country', [ 'Brazil', 'France', 'England', 'Italy'], 28, 10, '#fff', '#1e90ff', '#181818');
        this.playersChart('#club-svg', 'clubConfed', ['UEFA', 'CONMEBOL', 'CAF', 'AFC', 'CONCACAF', 'None'], 35, 12, '#fff', '#1e90ff', '#181818');
    },
    components:{
      AppHeader
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="scss" scoped>
    $card-width: 28vw;
    $small-screen: "only screen and (max-width : 600px)";
    #bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #181818;
      z-index: -10000;
    }
    h1, h2, h3, p{
      color: #fff;
    }
    #positions, #national-team, #club, #video{
      margin: 30px 0;
    }
    svg{
      border: 1px solid #212121;
    }
    #video{
      a{
        font-size: calc(#{$card-width} * .07);
        text-decoration: none;
        color: #1e90ff;
        &:hover{
          text-decoration: underline;
        }
        span{
          color: #b31217;
        }
      }
    }
  </style>



