<template>
  <div class="bond">
      <div id="bg"></div>
      <AppHeader></AppHeader>
        <div class="chart-component">
            <h1>The 24 Films of Bond</h1>
            <p>
                The following charts explores how much public interest there is for the James Bond frnachise.
                <br />They also explore the financial aspects of each film.
            </p>
            <div id="grossing">
                <h2>Grossing</h2>
                <p>The James Bond franchise have made a total of more than $6 billion. Here's how much each film made.</p>
                <input type="checkbox" name="gross" class="gross-toggle" id="gross-toggle" v-model="adjusted.gross" selected>
                <label for="gross-toggle">
                    <span v-if="!adjusted.gross">Adjust For Inflation (2005 Values)</span>
                    <span v-if="adjusted.gross">Display Actual Values</span>
                </label>
                <svg id="gross-svg" width="700" viewBox="0 0 700 460" preserveAspectRatio style="width: 100%;">
                    <defs>
                        <filter x="0" y="0" height="1" width="1" id="solid">
                            <feFlood flood-color="#332f2f"/>
                            <feComposite in="SourceGraphic" />
                        </filter>
                    </defs>
                </svg>
            </div>
            <div id="budget">
                <h2>Budget</h2>
                <p>Here's how much each film cost to produce.</p>
                <input type="checkbox" name="budget" class="budget-toggle" id="budget-toggle" v-model="adjusted.budget" selected>
                <label for="budget-toggle">
                    <span v-if="!adjusted.budget">Adjust For Inflation (2005 Values)</span>
                    <span v-if="adjusted.budget">Display Actual Values</span>
                </label>
                <svg id="budget-svg" width="700" viewBox="0 0 700 460" preserveAspectRatio style="width: 100%;" />
            </div>
            <div id="actor">
                <h2>Actor</h2>
                <p>Seven actors have played James Bond over the past 50 years. This chart shows how much each actor performed
                    at the box office <br />*Values have been adjusted to 2005 inflation levels.
                </p>
                <svg id="actor-svg" width="700" viewBox="0 0 700 280" preserveAspectRatio style="width: 100%;" />
            </div>
            <div id="reception">
                <h2>Reception</h2>
                <p>
                    The James Bond franchise as a whole is one of the most popular and well recieved of all film franchises.
                    This chart shows the reception each of the 24 films recieved.
                </p>
                <svg id="reception-svg" width="700" viewBox="0 0 700 460" preserveAspectRatio style="width: 100%;">
                    <text id="y-axis-label" data-scoreType=selected>Rotten Tomatoes Score</text>
                </svg>
            </div>
        </div>
  </div>
</template>

<script>
    import AppHeader from './../AppHeader'
    import moneyForm from './../mixins/moneyform'
    import * as _ from 'lodash'
    import * as d3 from 'd3'
    import data from './../../../static/charts/bond.csv'
    export default {
        name: 'bond',
        data () {
            return {
                adjusted:{
                    gross: false,
                    budget: false
                },
                data: data
            }
        },
        mixins:[moneyForm],
        methods:{
                createBarChart: function(d, idSelect, xRange, yRange, textColor, rectColor){
                    var moneyForm = this.moneyForm;
                    var that = this;

                    var t = d3.transition()
                        .duration(500)
                        .ease(d3.easeQuadInOut);

                    var x = d3.scaleBand()
                        .rangeRound(xRange)
                        .padding(0.1)
                        .domain(d.map(function(d){
                            return d.title;
                        }))

                        /*d3.min((d.map(function(d){
                            return parseFloat(d.gross)*.85;
                        })))*/

                    var yGross = d3.scaleLinear()
                        .domain([d3.max(d.map(function(d){
                            return parseFloat(d[idSelect])*1.1;
                        })),
                        d3.min(d.map(function(d){
                            return parseFloat(d[idSelect])*.85;
                        }))])
                        .range(yRange)

                    var grossingChart = d3.select('#'+idSelect+'-svg')
                        .append('g')
                        .attr('id', idSelect+'-chart')

                    grossingChart.append('g')
                        .attr('id', idSelect+'x-axis')
                        .attr('transform','translate(70, 320)')
                        .call(d3.axisBottom(x)
                            //.tickSize(-700, 0 ,0)
                            .ticks(24)
                            .tickFormat(function(d){return d}))
                        .selectAll('text')
                        .attr('fill', textColor)
                        .attr('text-anchor', 'start')
                        .attr("transform", "rotate(60)")

                    d3.select('#'+idSelect+'x-axis')
                        .selectAll('path')
                        .attr('stroke', textColor)

                    d3.select('#'+idSelect+'x-axis')
                        .selectAll('line')
                        .attr('stroke', textColor)

                    grossingChart.append('g')
                        .attr('id', idSelect+'y-axis')
                        .attr('transform','translate(60, 10)')
                        .call(d3.axisLeft(yGross)
                            //.tickSize(-700, 0 ,0)
                            .ticks(10)
                            .tickFormat(function(d){return moneyForm(d, true)}))
                            .selectAll('text')
                            .attr('fill', textColor)

                        d3.select('#'+idSelect+'y-axis')
                            .selectAll('path')
                            .attr('stroke', textColor)

                        d3.select('#'+idSelect+'y-axis')
                            .selectAll('line')
                            .attr('stroke', textColor)

                    grossingChart.append('g')
                        .attr('id', idSelect+'BarChart')
                        .attr('transform','translate(10, 0)')
                        .selectAll('rect')
                        .data(d)
                        .enter()
                        .append('rect')
                        .attr('fill', rectColor)
                        .attr('class', idSelect+'Bar')
                        .attr('width', 20)
                        .attr('height', function(d){
                            return 300-yGross(d[idSelect])
                        })
                        .attr('x', function(d, i){
                            return x(d.title)+50
                        })
                        .attr('y', function(d, i){
                            return yGross(d[idSelect]) + 10
                        })
                        .attr('data-title', function(d){
                            return d.title
                        })
                        .attr('data-actor', function(d){
                            return d.actor
                        })
                        .attr('data-year', function(d){
                            return d.year
                        })
                        .attr('data-budget', function(d){
                            return d.budget
                        })
                        .attr('data-adjusted-budget', function(d){
                            return d.adjustedbudget
                        })
                        .attr('data-gross', function(d){
                            return d.gross
                        })
                        .attr('data-adjusted-gross', function(d){
                            return d.adjustedgross
                        })
                        .attr('data-reception', function(d){
                            return d.reception
                        })
                        .on('mouseenter', function(d){
                            var elem = d3.select(this);
                            var d = d;
                            d3.select('#'+idSelect+'BarChart')
                                .append('g')
                                .attr('id', 'hovertext')
                                .append('text')
                                .attr('x', elem.attr('x'))
                                .attr('y', function(){
                                    if(that.adjusted[idSelect]){
                                        return yGross(d['adjusted'+idSelect]) + 5
                                    }else{
                                        return yGross(d[idSelect]) + 5
                                    }
                                })
                                .attr('text-anchor', 'middle')
                                .attr('fill', textColor)
                                .attr('filter', 'url(#solid)')
                                .text(function(){
                                    if(that.adjusted[idSelect]){
                                        return moneyForm(d['adjusted'+idSelect])
                                    }else{
                                        return moneyForm(d[idSelect])
                                    }
                                })
                        })
                        .on('mouseleave', function(d){
                            d3.select('#hovertext').remove()
                        })

                    function update(){
                        yGross = d3.scaleLinear()
                            .domain([d3.max(d.map(function(d){
                                if(that.adjusted[idSelect]){
                                    return parseFloat(d['adjusted'+idSelect])*1.1;
                                }else{
                                    return parseFloat(d[idSelect])*1.1;
                                }
                            })),
                            d3.min(d.map(function(d){
                                if(that.adjusted[idSelect]){
                                    return parseFloat(d['adjusted'+idSelect])*.85;
                                }else{
                                    return parseFloat(d[idSelect])*.85;
                                }
                            }))])
                            .range(yRange)

                        d3.select('#'+idSelect+'y-axis')
                            .call(d3.axisLeft(yGross)
                                //.tickSize(-700, 0 ,0)
                                .ticks(10)
                                .tickFormat(function(d){return moneyForm(d, true)}))
                                .selectAll('text')
                                .attr('fill', textColor)

                            d3.select('#'+idSelect+'y-axis')
                            .selectAll('line')
                            .attr('stroke', textColor)


                        d3.selectAll('.'+idSelect+'Bar')
                            .each(function(){
                                d3.select(this)
                                    .transition(t)
                                    .attr('y', function(d, i){
                                        if(that.adjusted[idSelect]){
                                            return yGross(d3.select(this).attr('data-adjusted-'+idSelect)) + 10
                                        }else{
                                            return yGross(d3.select(this).attr('data-'+idSelect)) + 10
                                        }
                                    })
                                    .attr('height', function(){
                                        if(that.adjusted[idSelect]){
                                            return 300-yGross(d3.select(this).attr('data-adjusted-'+idSelect))
                                        }else{
                                            return 300-yGross(d3.select(this).attr('data-'+idSelect))
                                        }
                                    })
                            });

                    }

                    d3.selectAll('.'+idSelect+'-toggle')
                        .on('change', function(){
                            update()
                        })
                },
                createActorChart: function(d, textColor, rectColor, rectStroke){
                    var films = d;
                    var moneyForm = this.moneyForm;
                    var actors = ["Sean Connery", "George Lazenby", "Roger Moore", "Timothy Dalton", "Pierce Brosnan", "Daniel Craig"];
                    var scale = d3.scaleLinear()
                        .domain([230, 2000])
                        .range([35, 300]);

                    var actorSVG = d3.select('#actor-svg');

                        actorSVG.append('g')
                            .attr('id', 'actor-labels')
                            .selectAll('text')
                            .data(actors)
                            .enter()
                            .append('text')
                            .attr('fill', textColor)
                            .attr('text-anchor', 'end')
                            .attr('x', 120)
                            .attr('y', function(d, i){
                                return i * 40 + 50;
                            })
                            .text(function(d){
                                return d;
                            });

                    var filmBars = actorSVG.append('g')
                        .attr('id', 'film-bars')

                    filmBars.selectAll('g')
                        .data(actors)
                        .enter()
                        .append('g')
                        .attr('transform', function(d, i){
                            return 'translate(130 '+ parseInt((i * 40) + 35)+')'
                        })
                        .each(function(d, i){
                            var oldOffset = 0;
                            var newOffset = 0;
                            var parent = d3.select(this);
                            var theData = _.filter(films, function(o){
                                return o.actor == d;
                            });
                            var theSum = _.sumBy(theData, function(o){return parseFloat(o.adjustedgross)});
                            parent.selectAll('rect')
                                .data(theData)
                                .enter()
                                .append('rect')
                                .attr('width', function(d){
                                    return scale(d.adjustedgross)
                                })
                                .attr('height', 20)
                                .attr('x', function(d, i){
                                    newOffset += oldOffset;
                                    oldOffset = scale(d.adjustedgross)
                                    return newOffset;
                                })
                                .attr('fill', rectColor)
                                .attr('stroke', rectStroke)
                                .attr('stroke-width', 0.5)
                                .attr('data-title', function(d){
                                    return d.title
                                })
                                .attr('data-year', function(d){
                                    return d.year
                                })
                                .attr('data-gross-adjusted', function(d){
                                    return d.adjustedgross
                                })
                                .attr('data-tickoffset', function(d, i){
                                    return parseFloat(d3.select(this).attr('width')) + parseFloat(d3.select(this).attr('x'));
                                })
                                .on('mouseenter', function(d, i){
                                    var that = d3.select(this);
                                    parent.append('text')
                                        .attr('id', 'hovertext')
                                        .attr('filter', 'url(#solid)')
                                        .attr('font-size', 13)
                                        .attr('fill', textColor)
                                        .attr('x', function(){
                                            return that.attr('x')
                                        })
                                        .attr('y', function(){
                                            return that.attr('y') - 15;
                                        })
                                        .text(function(){
                                            return that.attr('data-title') + ' (' + that.attr('data-year') + ')'
                                        })
                                        .attr('text-anchor', function(){
                                            var bbox = parseFloat(d3.select(this).node().getBBox().width) + parseFloat(that.attr('data-tickoffset'));
                                            var svgWidth = parseFloat(d3.select('#actor-svg').attr('width'));
                                            if (bbox+ 27 > svgWidth){
                                                return 'end';
                                            }else{
                                                return 'start';
                                            }
                                        })
                                        .append('tspan')
                                        .text(moneyForm(that.attr('data-gross-adjusted')))
                                        .attr('x', that.attr('x'))
                                        .attr('dy', 12)
                                })
                                .on('mouseleave', function(d, i){
                                    d3.select('#hovertext').remove();
                                })

                            parent.append('text')
                                .attr('x', function(){
                                    return newOffset+oldOffset;
                                })
                                .attr('dy', 31)
                                .attr('fill', textColor)
                                .attr('font-size', 12)
                                .text(function(){
                                    return moneyForm(theSum)
                                })
                                .attr('text-anchor', function(){
                                    if(parseFloat(d3.select(this).node().getBBox().x) < 100){
                                        return 'start'
                                    }else{
                                        return 'end'
                                    }
                                })
                        })
                },
                createReceptionChart: function(d, textColor, circColor){
                    var x = d3.scaleBand()
                        .rangeRound([0, 600])
                        .padding(0.1)
                        .domain(d.map(function(d){
                            return d.title;
                        }))

                    var yReception = d3.scaleLinear()
                        .domain([100,
                        d3.min(d.map(function(d){
                            return parseFloat(d.reception)-5;
                        }))])
                        .range([1,300])

                    var receptionScale = d3.scaleLinear()
                        .domain([100,
                        d3.min(d.map(function(d){
                            return parseFloat(d.reception)-5;
                        }))])
                        .range([8, 2])

                    var receptionChart = d3.select('#reception-svg')
                        .append('g')
                        .attr('id', 'reception-chart')

                    receptionChart.append('g')
                        .attr('id', 'reception-x-axis')
                        .attr('transform','translate(60, 320)')
                        .call(d3.axisBottom(x)
                            //.tickSize(-700, 0 ,0)
                            .ticks(24)
                            .tickFormat(function(d){return d}))
                        .selectAll('text')
                        .attr('fill', textColor)
                        .attr('text-anchor', 'start')
                        .attr("transform", "rotate(60)")

                    d3.select('#reception-x-axis')
                        .selectAll('path')
                        .attr('stroke', textColor)

                    d3.select('#reception-x-axis')
                        .selectAll('line')
                        .attr('stroke', textColor)

                    receptionChart.append('g')
                        .attr('id', 'reception-y-axis')
                        .attr('transform','translate(50, 10)')
                        .call(d3.axisLeft(yReception)
                            //.tickSize(-700, 0 ,0)
                            .ticks(10)
                            .tickFormat(function(d){return d}))

                    d3.select('#y-axis-label')
                        .attr('x', -750/5)
                        .attr('y', 20)
                        .attr('fill', textColor)
                        .attr('text-anchor', 'middle')
                        .attr('transform', 'rotate(-90)')

                    d3.select('#reception-y-axis')
                        .selectAll('text')
                        .attr('fill', textColor)

                    d3.select('#reception-y-axis')
                        .selectAll('path')
                        .attr('stroke', textColor)

                    d3.select('#reception-y-axis')
                        .selectAll('line')
                        .attr('stroke', textColor)

                receptionChart.append('g')
                        .attr('id', 'receptionDot')
                        .attr('transform','translate(10, 0)')
                        .selectAll('circle')
                        .data(d)
                        .enter()
                        .append('circle')
                        .attr('fill', circColor)
                        .attr('class', 'receptionDot')
                        .attr('data-reception', function(d){
                            return d.reception
                        })
                        .attr('r', function(d){
                            return receptionScale(d.reception)
                        })
                        .attr('cx', function(d, i){
                            return x(d.title)+50
                        })
                        .attr('cy', function(d, i){
                            return yReception(d.reception) + 10
                        })
                        .attr('data-reception', function(d){
                            return d.reception
                        })
                        .attr('data-title', function(d){
                            return d.title
                        })
                        .attr('data-year', function(d){
                            return d.year
                        })
                        .on('mouseenter', function(d){
                            var that = d3.select(this);
                            d3.select('#receptionDot')
                                .append('g')
                                .attr('id', 'hovertext');

                            d3.select('#hovertext')
                                .append('line')
                                .attr('x1', function(){
                                    return that.attr('cx')
                                })
                                .attr('y1', function(){
                                    return that.attr('cy')
                                })
                                .attr('x2', function(){
                                    return that.attr('cx')
                                })
                                .attr('y2', function(){
                                    return 320;
                                })
                                .attr('stroke', textColor)
                                .attr('stroke-width', .5)
                            d3.select('#hovertext')
                                .append('text')
                                .attr('id', 'filmInfo')
                                .attr('x', that.attr('cx'))
                                .attr('dy', function(){
                                    return parseInt(that.attr('cy'))+25
                                })
                                .attr('text-anchor', function(){
                                        return 'middle'
                                })
                                .attr('fill', textColor)
                                .attr('filter', 'url(#solid)')
                                .text(function(){
                                    return that.attr('data-reception') + '%';
                                })
                        })
                        .on('mouseleave', function(d){
                            d3.select('#hovertext').remove()
                        })
                }
        },
        mounted(){
            window.scrollTo(0, 0);
            document.title = 'The 24 Films of Bond - Charts';
            var moneyForm = this.moneyForm;
            var createBarChart = this.createBarChart;
            var createActorChart = this.createActorChart;
            var createReceptionChart = this.createReceptionChart;
                createBarChart(data, 'gross', [0, 600], [1, 300], '#c8932b', '#c8932b');
                createBarChart(data, 'budget', [0, 600], [1, 300], '#c8932b', '#c8932b');
                createReceptionChart(data, '#c8932b', '#c8932b')
                createActorChart(data, '#c8932b', '#c8932b', 'black');

        },
        components:{
            AppHeader
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    $card-width: 28vw;
    $small-screen: "only screen and (max-width : 600px)";
    #bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #332f2f;
        z-index: -10000;
    }

    input{
        display: none;
        box-sizing: border-box;
    }

    label{
        border: 2px solid #c8932b;
        padding: calc(#{$card-width} * .03);
        &:hover{
            color: #332f2f;
            background-color: #c8932b
        }
    }

    h1, h2, p, label{
        color: #c8932b
    }
    svg{
        margin: 30px 0;
    }
</style>



