<script>
    import {formatMoney} from 'accounting'
    export default{
        name: 'moneyform',
        methods:{
            moneyForm: function (cash, short){
                if (parseInt(cash).toString().length > 3){
                    return formatMoney(cash/1000, '$', 2) + (short ? 'B' : ' Billion')
                }else{
                    return formatMoney(cash, '$', 1) + (short ? 'M' : ' Million')
                }
            }
        }
    }
</script>


