    <template>
    <div class="ohsixonesix">
        <div id="bg"></div>
        <AppHeader></AppHeader>
        <div class="chart-component">
            <div id="header">
                <h1>10 Years of Film in Money</h1>
                <p>The following charts show the worldwide gross and budgets of films from 2006 and 2016.</p>
            </div>
            <div id="grossing">
                <h2>Grossing</h2>
                <p>The top 25 grossing movies of 2006 made a total of {{getTotal(data.six, 'gross.worldwide')}}, while 2016's top grossing movies 
                    made {{getDifference(data.sixteen, data.six, 'gross.worldwide')}} more at {{getTotal(data.sixteen, 'gross.worldwide')}}.</p>
                <svg id="grossingChart" width="700" viewBox="0 0 700 200" preserveAspectRatio style="width: 100%;" />
            </div>
            <div id="budget">
                <h2>Budget</h2>
                <p>The top 25 grossing movies of 2006 cost a total of {{getTotal(data.six, 'budget')}}, while 2016's top grossing movies 
                    cost {{getDifference(data.sixteen, data.six, 'budget')}} more at {{getTotal(data.sixteen, 'budget')}}.</p>
                <svg id="budgetChart" width="700" viewBox="0 0 700 200" preserveAspectRatio style="width: 100%;" />
            </div>
            <div id="roi">
                <h2>Return on Investment</h2>
                <div id="roi-flex">
                    <div id="roi06">
                        <h3>2006</h3>
                        <p>{{getRoi(data.six, 'gross.worldwide', 'budget')}}</p>
                    </div>
                    <div id="roi16">
                        <h3>2016</h3>
                        <p>{{getRoi(data.sixteen, 'gross.worldwide', 'budget')}}</p>
                    </div>
                </div>
            </div>
            <div id="source">
                <h2>Source Material</h2>
                <svg id="sourceChart" width="700" viewBox="0 0 700 400" preserveAspectRatio style="width: 100%;" />
            </div>
        </div>
    </div>
    </template>

    <script>
        import AppHeader from './../AppHeader'
        import data from './../../../static/charts/0616.json'
        import moneyForm from './../mixins/moneyform'
        import {sumBy, map, orderBy, filter} from 'lodash'
        import * as d3 from 'd3'
        export default {
            name: 'ohsixonesix',
            data () {
                return {
                    data: data,
                }
            },
            mixins:[moneyForm],
            methods:{
                getTotal: function(theData, prop){
                    return this.moneyForm(sumBy(theData, prop));
                },
                getDifference: function(theData1, theData2, prop ){
                    var theDifference = sumBy(theData1, prop) - sumBy(theData2, prop);
                    return this.moneyForm(theDifference);
                },
                getRoi: function(theData, revenueProp, costProp){
                     var theDifference = sumBy(theData, revenueProp)- sumBy(theData, costProp);
                     var divided = theDifference / sumBy(theData, costProp);
                     return (divided * 100).toFixed(2) + '%'
                },
                makeBarChart: function(theData, props, domain, range, colorRange, tickColor, textColor, idSelect, customId, year, y){
                    var moneyForm = this.moneyForm
                    var thisChartScale = d3.scaleLinear()
                    .domain(domain)
                    .range(range);

                    var colorScaleArr = map(theData, props);                

                    var thisChartColorScale = d3.scaleLinear()
                        .domain([d3.min(colorScaleArr.map(function(d){
                            return parseFloat(d)*1.1;    
                        })),
                        d3.max(colorScaleArr.map(function(d){
                            return parseFloat(d)*.85;    
                        }))])
                        .range(colorRange);
                    var oldOffset = 0;
                    var newOffset = 0;
                    var currentColor = null;
                    var thisChartDiv = d3.select('#'+idSelect);
                    
                    thisChartDiv.append('text')
                        .text(year)
                        .attr('fill', textColor)
                        .attr('x', 5)
                        .attr('y', y);

                    thisChartDiv.append('g')
                        .attr('id', idSelect+customId)
                        .selectAll('rect')
                        .data(orderBy(theData, [props], ['desc']))
                        .enter()
                        .append('rect')
                        .attr('fill', function(d, i){
                            var propsArray = props.split('.');
                            if(propsArray.length > 1){
                                return thisChartColorScale(d[propsArray[0]][propsArray[1]]);
                            }else{
                                return thisChartColorScale(d[propsArray[0]]);
                            }
                        })
                        .attr('stroke', '#efe6d0')
                        .attr('stroke-width', '.5')
                        .attr('width', function(d, i){
                            var propsArray = props.split('.');
                            if(propsArray.length > 1){
                                return thisChartScale(d[propsArray[0]][propsArray[1]]);
                            }else{
                                return thisChartScale(d[propsArray[0]]);
                            }
                        })
                        .attr('height', 30)
                        .attr('x', function(d, i){
                            newOffset += oldOffset;
                            var propsArray = props.split('.');
                            if(propsArray.length > 1){
                                oldOffset = thisChartScale(d[propsArray[0]][propsArray[1]]);
                            }else{
                                oldOffset = thisChartScale(d[propsArray[0]]);
                            }
                            return newOffset + 50
                        })
                        .attr('y', y-20)
                        .attr('data-title', function(d, i){
                            return d.title;
                        })
                        .attr('data-gross', function(d, i){
                            var propsArray = props.split('.');
                            if(propsArray.length > 1){
                                return d[propsArray[0]][propsArray[1]];
                            }else{
                                return d[propsArray[0]];
                            }
                        })
                        .attr('data-tickoffset', function(d, i){
                            return parseFloat(d3.select(this).attr('width')) + parseFloat(d3.select(this).attr('x'));
                        })
                        .on('mouseenter', function(d, i){
                            currentColor = d3.select(this).attr('fill');
                            d3.select(this).attr('fill', tickColor);
                            var textAnchor = 'end';
                            var that = d3.select(this);
                            thisChartDiv.append('g')
                                .attr('id', 'thisChartTick')
                            d3.select('#thisChartTick')
                                .append('line')
                                .attr('id', 'thisChartTickLine')
                                .attr('x1', that.attr('data-tickoffset'))
                                .attr('y1', y-30)
                                .attr('x2', that.attr('data-tickoffset'))
                                .attr('y2', y+10)
                                .attr('style', 'stroke:'+tickColor+';stroke-width:0.1');

                            d3.select('#thisChartTick')
                                .append('text')
                                .attr('fill', textColor)
                                .attr('font-size', '14px')
                                .attr('id', 'thisChartTitleText')
                                .attr('x', that.attr('data-tickoffset'))
                                .attr('dy', y-40)
                                .append('tspan')
                                .text(that.attr('data-title'))

                            d3.select('#thisChartTitleText')
                                .append('tspan')
                                .attr('x', that.attr('data-tickoffset'))
                                .attr('dy', 15)
                                .text(moneyForm(that.attr('data-gross')));

                            d3.select('#thisChartTitleText')
                                .attr('text-anchor', function(){
                                    var bbox = parseFloat(d3.select(this).node().getBBox().width) + parseFloat(that.attr('data-tickoffset'));
                                    var svgWidth = parseFloat(d3.select('#'+idSelect).attr('width'));
                                    if (bbox > svgWidth){
                                        textAnchor = 'end';
                                    }else{
                                        textAnchor = 'start';
                                    }
                                    return textAnchor;
                                })
                        })
                        .on('mouseleave', function(){
                            d3.select(this).attr('fill', currentColor)                  
                            d3.select('#thisChartTick').remove();
                        });

                    thisChartDiv.append('line')
                        .attr('x1', newOffset+oldOffset + 50)
                        .attr('y1', y-20)
                        .attr('x2', newOffset+oldOffset + 50)
                        .attr('y2', y+30)
                        .attr('style', 'stroke:'+ tickColor+';stroke-width:0.1');

                    thisChartDiv.append('text')
                        .text(moneyForm(sumBy(theData, props)))
                        .attr('fill', textColor)
                        .attr('font-size', '14px')
                        .attr('text-anchor', 'end')
                        .attr('x', newOffset+oldOffset+50)
                        .attr('y', y+30);
                },
                makeSourceChat: function (theData, year, yearLoc, translate, LorR, textColor, mainColors, derivativeColors){
                    var derivatives = filter(theData, function(o){
                        return  o.source != 'original'
                    });
                    var originals = filter(theData, function(o){
                        return  o.source == 'original'
                    });
                    
                    var derivativeTypes = {
                        sequel: filter(derivatives, function(o){
                            return  o.source == 'sequel'
                        }),
                        novel: filter(derivatives, function(o){
                            return  o.source == 'novel'
                        }),
                        comic: filter(derivatives, function(o){
                            return  o.source == 'comic'
                        }),
                        remake: filter(derivatives, function(o){
                            return  o.source == 'remake'
                        }),
                        spinOff: filter(derivatives, function(o){
                            return  o.source == 'spin-off'
                        }),
                        fairyTale: filter(derivatives, function(o){
                            return  o.source == 'fairy tale'
                        }),
                        videoGame: filter(derivatives, function(o){
                            return  o.source == 'video game'
                        }),
                        toy: filter(derivatives, function(o){
                            return  o.source == 'toy'
                        })
                    }
                
                    var mainColorRange = mainColors
                    var mainColor = d3.scaleOrdinal()
                        .range(mainColorRange);

                    var arcChart = d3.select('#sourceChart')

                    arcChart.append('g')
                    .append('text')
                    .text(year)
                    .attr('x', yearLoc[0])
                    .attr('y', yearLoc[1])
                    .attr('fill', textColor)
                    
                    var mainSourcesChart = arcChart.append('g')
                        .attr('class', 'chart')
                        .attr('transform', 'translate('+translate+')')
                        .attr('id','mainSourcesChart'+year);

                    var mainSourcesArc = d3.arc()
                        .innerRadius(140)
                        .outerRadius(90)
                
                    var mainPieData = d3.pie()
                        .startAngle(0)
                        .endAngle(function(){
                            if(LorR == 'left'){
                                return (Math.PI/180) * -180
                            }else{
                                return (Math.PI/180) * 180
                            }
                        })([derivatives.length, originals.length])

                    var pieAngles = {
                        starts: [],
                        ends: []
                    };

                    mainSourcesChart.selectAll('path')
                        .data(mainPieData)
                        .enter()
                        .append('path')
                        .attr('class', 'mainChart'+year)
                        .attr('d', mainSourcesArc)
                        .attr('stroke', '#efe6d0')
                        .attr('fill', function(d){
                            pieAngles.starts.push(d.startAngle);
                            pieAngles.ends.push(d.endAngle);
                            return  mainColor(d.data)
                        });


                    var mainSourcesCentroids = [];
                    var mainSourcesLines = d3.select('#mainSourcesChart'+year)
                        .selectAll('polyline')
                        .data(mainPieData)
                        .enter()
                        .append('polyline')
                        .attr('fill', 'none')
                        .attr('stroke', 'grey')
                        .attr('stroke-width', 0.3)
                        .attr('points', function(d){
                            var cento = mainSourcesArc.centroid(d)
                            if(d.index == 0){
                                if (LorR == 'left'){
                                    mainSourcesCentroids.push([cento[0], cento[1]+15, cento[0], cento[1]+30, cento[0] + 30, cento[1] + 55]);
                                    return [cento[0], cento[1]+15, cento[0], cento[1]+30, cento[0] + 30, cento[1] + 50];
                                }else{
                                    mainSourcesCentroids.push([cento[0], cento[1]-5, cento[0], cento[1]+10, cento[0] - 50, cento[1] + 30]);
                                    return [cento[0], cento[1]-5, cento[0], cento[1]+10, cento[0] - 40, cento[1] + 30];
                                }
                            }else{
                                if (LorR == 'left'){
                                    mainSourcesCentroids.push([cento[0], cento[1]-15, cento[0], cento[1]-30, cento[0] + 30, cento[1] - 45]);
                                    return [cento[0], cento[1]-15, cento[0], cento[1]-30, cento[0] + 30, cento[1] - 50];
                                }else{
                                    mainSourcesCentroids.push([cento[0], cento[1]-5, cento[0], cento[1]-25, cento[0] - 30, cento[1] - 55]);
                                    return [cento[0], cento[1]-5, cento[0], cento[1]-25, cento[0] - 25, cento[1] - 40];
                                }
                                
                            }
                        })

                        var mainSourcesText = d3.select('#mainSourcesChart'+year)
                            .selectAll('text')
                            .data(mainPieData)
                            .enter()
                            .append('text')
                            .attr('font-size', '14')
                            .attr('fill', function(d){
                                return mainColorRange[d.index]
                            })
                            .attr('x', function(d){
                                return mainSourcesCentroids[d.index][4]+5
                            })
                            .attr('y', function(d){
                                return mainSourcesCentroids[d.index][5]
                            })
                            .attr('text-anchor', function(d){
                                if (LorR == 'left'){
                                    return 'start';
                                }else{
                                    return 'end';
                                }
                            })
                            .text(function(d){
                                var type = ['Derivatives', 'Original Scripts']
                                return type[d.index];
                            })
                            .append('tspan')
                            .attr('x', function(d){
                                return mainSourcesCentroids[d.index][4]+5
                            })
                            .attr('dy', 18)
                            .text(function(d){
                                return d.value + ' Films' + ' ('+(d.value/25) * 100 + '%)';
                            })


                    var derivativesColorRange = derivativeColors;
                    var derivativesColor = d3.scaleOrdinal()
                        .range(derivativesColorRange);

                    var derivativesChart = arcChart.append('g')
                        .attr('transform', 'translate('+translate+')')
                        .attr('id','derivativesChart'+year);

                    var derivativeTypesData = [derivativeTypes.sequel.length, 
                        derivativeTypes.novel.length,
                        derivativeTypes.comic.length,
                        derivativeTypes.remake.length,
                        derivativeTypes.spinOff.length,
                        derivativeTypes.fairyTale.length,
                        derivativeTypes.videoGame.length,
                        derivativeTypes.toy.length];

                    var derivativesPieData = d3.pie()
                        .startAngle(0)
                        .endAngle(pieAngles.ends[0])(derivativeTypesData);

                    var derivativesArc = d3.arc()
                        .innerRadius(160)
                        .outerRadius(140);

                    derivativesChart.selectAll('path')
                        .data(derivativesPieData)
                        .enter()
                        .append('path')
                        .attr('class', 'derivativesChartPath')
                        .attr('d', derivativesArc)
                        .attr('stroke', '#efe6d0')
                        .attr('fill', function(d, i){
                            return derivativesColorRange[i]
                        });
                        
                    var derivativeSourcesCentroids = [];
                    var derivativeSourcesLines = d3.select('#derivativesChart'+year)
                        .selectAll('polyline')
                        .data(derivativesPieData)
                        .enter()
                        .append('polyline')
                        .attr('class', 'dslines')
                        .attr('data-value', function(d){
                            return d.value;
                        })
                        .attr('fill', 'none')
                        .attr('stroke', 'grey')
                        .attr('stroke-width', 0.3)
                        .attr('points', function(d,i){
                            var cento = derivativesArc.centroid(d)
                            if(LorR == 'left'){
                                derivativeSourcesCentroids.push([cento[0], cento[1]-5, cento[0]-5, cento[1]+5, cento[0] - 40, cento[1] - 5]);
                                return [cento[0], cento[1]-5, cento[0]-5, cento[1]+5, cento[0] - 40, cento[1] -5 ];
                            }else{
                                derivativeSourcesCentroids.push([cento[0], cento[1]-5, cento[0]+5, cento[1]+5, cento[0] + 35, cento[1]]);
                                return [cento[0], cento[1]-5, cento[0]+5, cento[1]+5, cento[0] + 25, cento[1] -5 ];
                            }
                                
                        });
                        
                    var derivativeSourcesText = d3.select('#derivativesChart'+year)
                            .selectAll('text')
                            .data(derivativesPieData)
                            .enter()
                            .append('text')
                            .attr('class', 'dstext')
                            .attr('data-value', function(d){
                                return d.value;
                            })
                            .attr('font-size', '12')
                            .attr('text-anchor', function(){
                                if(LorR == 'left'){
                                    return 'end'
                                }else{
                                    return 'start';
                                }
                            })
                            .attr('fill', function(d){
                                return derivativesColorRange[d.index]
                            })
                            .attr('x', function(d){
                                return derivativeSourcesCentroids[d.index][4]+5
                            })
                            .attr('y', function(d){
                                return derivativeSourcesCentroids[d.index][5]
                            })
                            .attr('dx', '-10')
                            .text(function(d){
                                var types = ['Sequel', 'Novel', 'Comic', 'Remake', 'Spin-off', 'Fairy Tale', 'Video Game', 'Toy']
                                    return derivativeTypesData[d.index] + ' ' + types[d.index] + (derivativeTypesData[d.index] > 1 ? 's' : '');
                            })

                            d3.selectAll('polyline[data-value="0"]').remove();
                            d3.selectAll('text[data-value="0"]').remove();
                    }
            },
            mounted(){
                window.scrollTo(0, 0);
                document.title = '0616 - Charts';
                var makeBarChart = this.makeBarChart;
                var makeSourceChat = this.makeSourceChat;
                
                makeBarChart(data.six, 'gross.worldwide', [100, 2200], [10, 70], ['rgb(136, 175, 204)', 'rgb(37, 99, 198)'], 'rgb(21, 73, 155)', '#021c30','grossingChart', 'OhSix', '2006', 60);
                makeBarChart(data.sixteen, 'gross.worldwide', [100, 2200], [10, 70], ['rgb(204, 158, 136)', 'rgb(198, 98, 61)'], 'rgb(175, 80, 45)', '#021c30', 'grossingChart', 'OhSixteen', '2016', 160);
                makeBarChart(data.six, 'budget', [10, 1300], [10, 160], ['rgb(136, 175, 204)', 'rgb(37, 99, 198)'], 'rgb(21, 73, 155)', '#021c30', 'budgetChart', 'OhSix', '2006', 60);
                makeBarChart(data.sixteen, 'budget', [10, 1300], [10, 160], ['rgb(204, 158, 136)', 'rgb(198, 98, 61)'], 'rgb(175, 80, 45)', '#021c30', 'budgetChart', 'OhSixteen', '2016', 160);

                makeSourceChat(data.six, '2006', [180, 25], [250, 200], 'left', '#021c30', ["rgb(198, 98, 61)", "rgb(204, 158, 136)"], ['#002147', '#00356b', '#003153', '#6082b6', '#40826d', '	#6699cc', '#008080', '#2e5090']);
                makeSourceChat(data.sixteen, '2016', [450, 25], [425, 200], 'right', '#021c30', ["rgb(198, 98, 61)", "rgb(204, 158, 136)"], ['#002147', '#00356b', '#003153', '#6082b6', '#40826d', '#6699cc', '#008080', '#2e5090']);
            },
            components:{
                AppHeader
            }
        }
    </script>

    <!-- Add "scoped" attribute to limit CSS to this component only -->
    <style lang="scss" scoped>
        $card-width: 28vw;
        $small-screen: "only screen and (max-width : 600px)";
        #bg{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #e8ded3;
            z-index: -10000;
        }
        h1, h2, h3, p{
            color: #021c30;
        }

        #roi{
            display: none;
        }

        #roi-flex{
            display: flex;
            flex-direction: row;
            text-align: center;
            justify-content: center;
            div{
                flex-grow: 1;
                &:first-child{
                    border-right: 1px solid violet;
                }
            }
            h3{
                font-size: 2vw;
            }
            p{
                font-size: 2vw;
            }
        }
    </style>


