    <template>
    <div class="pixar">
        <div id="bg"></div>
        <AppHeader></AppHeader>
        <div class="chart-component">
            <p>The following charts the critical reception, worldwide gross, and the year of every Pixar feature length release.</p>
            <form id="the-form" v-bind:data-input-value=selected>
                <input type="radio" name="scoretype" id="rt-critics" value="Critics" v-model="selected" selected>
                <label for="rt-critics">Critics' Score</label>
                <input type="radio" name="scoretype" id="rt-audience" value="Audience" v-model="selected">
                <label for="rt-audience">Audience Score</label>
            </form>
            <svg id="pixarChart" width="750" viewBox="0 0 750 450" preserveAspectRatio style="width: 100%;">
                <text id="y-axis-label" data-scoreType=selected>Rotten Tomatoes {{selected}}<tspan v-if='selected=="Critics"'>'</tspan> Score</text>
            </svg>
        </div>
    </div>
    </template>

    <script>
        import AppHeader from './../AppHeader'
        import data from './../../../static/charts/pixar.csv'
        import moneyForm from './../mixins/moneyform'
        import * as _ from 'lodash'
        import * as d3 from 'd3'
        export default {
            name: 'pixar',
            data () {
                return {
                    selected: 'Critics',
                    data: data

                }
            },
            mixins:[moneyForm],
            methods:{
                createDotPlot: function(d){
                    
                }
            },
            mounted(){
                window.scrollTo(0, 0);
                document.title = 'Critical Reception of Pixar - Charts';
                var scoreType = this.selected.toLowerCase();
                var moneyForm = this.moneyForm;
                var that = this;

                    var d = data;
                    var scale = d3.scaleLinear()
                        .domain([300, 1200])
                        .range([10, 50])

                    var x = d3.scaleLinear()
                        .domain([1994, 2017])
                        .range([0, 650]);

                    var y = d3.scaleLinear()
                        .domain([105, 35])
                        .range([35, 380]);

                    var t = d3.transition()
                        .duration(500)
                        .ease(d3.easeQuadInOut);

                    d3.select('#pixarChart')
                        .append('defs')
                        .append('filter')
                        .attr('x', 0)
                        .attr('y', 0)
                        .attr('width', 1)
                        .attr('height', 1)
                        .attr('id', 'solid')
                        .append('feFlood')
                        .attr('flood-color','#cccbc6');
                    
                    d3.select('#solid')
                        .append('feComposite')
                        .attr('in', 'SourceGraphic');
                    
                    var theChart = d3.select('#pixarChart')
                        .append('g')
                        .attr('id', 'theChart')
                        .attr('transform','translate(30, 20)');

                    theChart.append('g')
                        .attr('id', 'x-axis')
                        .attr('transform','translate(35, 370)')
                        .call(d3.axisBottom(x)
                            .ticks(20)
                            //.tickSize(-700, 0 ,0)
                            .tickFormat(d3.format("d")))
                            .selectAll('text')
                            .attr('fill', '#056571')

                    theChart.append('text')
                        .attr('id', 'x-axis-label')
                        .attr('x', 750/2)
                        .attr('y', 410)
                        .attr('text-anchor', 'middle')
                        .attr('fill', '#056571')
                        .text('Year of Release')

                    d3.select('#x-axis')
                        .selectAll('path')
                        .attr('stroke', '#056571')

                    d3.select('#x-axis')
                        .selectAll('line')
                        .attr('stroke', '#056571')

                    theChart.append('g')
                        .attr('id', 'y-axis')
                        .attr('transform','translate(30, -20)')
                        .call(d3.axisLeft(y)
                            //.tickSize(-650, 0 ,0)
                            .ticks(5)
                            .tickFormat(function(d){return d+'%'}))
                            .selectAll('text')
                            .attr('fill', '#056571')

                    d3.select('#y-axis')
                        .selectAll('path')
                        .attr('stroke', '#056571')

                    d3.select('#y-axis')
                        .selectAll('line')
                        .attr('stroke', '#056571')


                    d3.select('#y-axis-label')
                        .attr('x', -750/3.5)
                        .attr('y', 25)
                        .attr('text-anchor', 'middle')
                        .attr('fill', '#056571')
                        .attr('transform', 'rotate(-90)')

                    theChart.append('g')
                        .attr('id', 'dots')
                        .attr('transform','translate(35, -20)')
                        .selectAll('circle')
                        .data(d)
                        .enter()
                        .append('circle')
                        .attr('class', 'dots')
                        .attr('r', function(d){
                            return scale(d.gross);
                        })
                        .attr('cx', function(d){
                            return x(d.year)
                        })
                        .attr('cy', function(d){
                            return 0
                        })
                        .attr('stroke', '#056571')
                        .attr('fill', '#cda34f')
                        .attr('data-title', function(d){
                            return d.title;
                        })
                        .attr('data-year', function(d){
                            return d.year;
                        })
                        .attr('data-gross', function(d){
                            return d.gross;
                        })
                        .attr('data-critics', function(d){
                            return d.critics
                        })
                        .attr('data-audience', function(d){
                            return d.critics
                        })
                        .attr('data-audience', function(d){
                            return d.audience
                        })
                        .on('mouseenter', function(d){
                            var that = d3.select(this);
                            that.raise();
                            d3.select('#dots')
                                .append('g')
                                .attr('id', 'hovertext');

                            d3.select('#hovertext')
                                .append('text')
                                .attr('id', 'filmInfo')
                                .attr('x', that.attr('cx'))
                                .attr('dy', function(){
                                    if(that.attr('cy') > 360){
                                        return parseInt(that.attr('cy'))-50
                                    }else{
                                        return parseInt(that.attr('cy'))+50
                                    }
                                })
                                .attr('text-anchor', function(){
                                    if (that.attr('cx') > 570){
                                        return 'end'
                                    }else{
                                        return 'middle'
                                    }
                                })
                                .attr('fill', '#056571')
                                .attr('stroke', '#056571')
                                .attr('filter', 'url(#solid)')
                                .text(function(){
                                    return that.attr('data-title') + ' ('+ that.attr('data-year') +')'
                                })
                                .append('tspan')
                                .attr('x', that.attr('cx'))
                                .attr('dy', 20)
                                .text(function(){
                                    return moneyForm(that.attr('data-gross'));
                                })
                                d3.select('#filmInfo')
                                    .append('tspan')
                                    .attr('x', that.attr('cx'))
                                    .attr('dy', 15)
                                    .attr('font-size', 12)
                                    .text(function(){
                                        return 'RT: ' + that.attr('data-critics') + '% / ' + that.attr('data-audience') + '%'
                                    })
                        })
                        .on('mouseleave', function(d){
                            d3.select('#hovertext').remove()
                        })

                        function update(prop){
                            d3.selectAll('circle')
                                .each(function(){
                                    d3.select(this)
                                        .transition(t)
                                        .attr('cy', y(d3.select(this).attr('data-'+prop)));
                                })
                        }

                        d3.selectAll('input')
                            .on('change', function(){
                                update(that.selected.toLowerCase())
                            })

                        update(that.selected.toLowerCase())
                        


            },
            components:{
                AppHeader
            }
        }
    </script>

    <!-- Add "scoped" attribute to limit CSS to this component only -->
    <style lang="scss" scoped>
        $card-width: 28vw;
        $small-screen: "only screen and (max-width : 600px)";
        #bg{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #cccbc6;
            z-index: -10000;
        }

        h1, h2, h3, p{
            color: #056571;
        }

        input{
            display: none;
        }

        label{
            border: 1px solid #056571;
            border-radius: 5px;
            color: #056571;
            &:hover{
                color: #cccbc6;
                background-color: #056571;
            }
        }
        input:checked+label{
            color: #cccbc6;
            background-color: #056571;
        }
    </style>


