module.exports = {"data":[{"title":"0616","subtitle":"10 Years of Film in Money","desc":"I wanted to see how the financial aspect of film changed over the past 10 years; so I compiled a list of the top grossing 25 films from 2006 and compared them to the top 25 grossing films of 2016. I then made a chart to figure out the difference in revenue between 2006 and 2016; and how much more expensive or cheaper these movies were to produce. Also tried to figure out how original the scripts of 2006 were compared to the scripts of 2016.","id":"ohsixonesix"},{"title":"Zinedine Zidane","subtitle":"by Vuadville Smash","desc":"In 2014, Australian band VAUDEVILLE SMASH released their song “Zinedine Zidane” as a tribute to the former football player. The song featured the voice of football commentator Les Murray (also from Australia) listing off the names of 81 of world’s greatest footballers from the past and present. I made a few charts showing the position, national team and club of each player mentioned in the song.","id":"zidane"},{"title":"Pixar","subtitle":"Critical Reception","desc":"A dot plot of the critical reception of all 17 feature-length Pixar films from Toy Story to Finding Dory.","id":"pixar"},{"title":"Bond","supertitle":"The 24 Films Of","subtitle":"","desc":"I wanted to explore how much public interest there is for the James Bond movie franchise after more than 50 years of the first movie’s release. So I made a few charts about revenue generated by each movie as a measure of how many people wanted to go see each new release; the budget of each movie to gauge how much money movie studios were willing to invest in the franchise; and the critical reception of each movie.","id":"bond"}]}

