import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Chart from '@/components/Chart'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta:{
        title: 'bars&lines'
      }
    },
    {
      path: '/:id',
      name: 'Chart',
      component: Chart,
      meta:{
        scrollToTop: true
      }
    }
  ]
})



